import noData from '@/components/noData'
import back from '@/components/back'
import { setInterval } from 'timers';

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
			SpecialistHosGroupId:this.$route.query.SpecialistHosGroupId || 0,
			HosGroupName:this.$route.query.HosGroupName || '',
			search:this.$store.state.searchText,
			from:this.$route.query.from,//1,2
			Goods:[],
			defaultImg: 'this.src="' + require('../../assets/prefRecommend/1.jpg') + '"',
			GoodsNum:0,
			ListDetails:this.$store.state.preList,
			specsShow:false,
			carShow:false,
			spcData:null,
			chooseDrShow:false,
			page:1,
			page2:1,
			haveMore: true,
			haveMore2: true,
			inputFlag: false,
			pageShow: false,
			showConfirm: false,
			act2:null,
			list:[],
			drSearch:'',
			act3:null,
			user: this.$store.state.userInfo,
			title:"",
			actT:null,
			SpecialId:null,
			idxNum:0,
			GoodsTypeNo:'',
			istHos:false,
			RecommendType:null,
			yEvent:null,
			AreaId:this.$store.state.inspctArea?this.$store.state.inspctArea.AreaId:0,
			AreaName:this.$store.state.inspctArea?this.$store.state.inspctArea.AreaName:'',
			btnColor:true,
			strotArr:['','',''],
			//strotArr:['','','',''],
			sNum:0,
			OrderDirection:false,
			OrderByType:0,
			KeyWord:'',
			Level:[],
			dNum:0,
			dShow:false,
			tTop:0,
        }
    },
     computed:{
        totalPrice(){
            let p = 0
            if(this.ListDetails && this.ListDetails.length){
                this.ListDetails.forEach(v => {
    				if(v.GoodsSource == 1){
    					 p += (v.Price * v.GoodsNum)
    				}else{
    					p += (v.VipPrice * v.GoodsNum)
    				}
                })
            }
            return p.toFixed(2)
        },
    	totalM(){
    		let p = 0
    		if(this.ListDetails && this.ListDetails.length){
    		    this.ListDetails.forEach(v => {
    		        p += Number(v.RecommondAmount) * v.GoodsNum
    		    })
    		}
    		return p
    	}
    },
    created(){
        this.pullList()
		this.getSecondLevel()
    },
    mounted(){
		this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		strotChoose(idx){
			this.sNum = idx
			if(this.dShow){
				this.dShow = false
			}
			if(idx > 2){
				this.dShow = true
				setTimeout(()=>{
					this.getMore2()
					this.$refs.el2.scrollTop = this.tTop
				},100)
				
			}else{
				this.page = 1
				this.haveMore = true
				if(this.OrderDirection){
					this.OrderDirection = false
				}else{
					this.OrderDirection = true
				}
				this.OrderByType = idx
				this.pullList(false)
			}
		},
		DialogClick(item,idx){
			this.dNum = idx
			this.page = 1
			this.haveMore = true
			this.dShow = false
			if(idx < 1){
				this.KeyWord = ''
			}else{
				this.KeyWord = item.DepartmentName
			}
			this.pullList(false)
		},
		goDetail(i){
			if(i.GoodsSource != 1 && i.GoodsSource != 4){
				this.$router.push(`/prefRecommend/preDetail?id=${i.GoodsId}`)
			}
		},
		alertMsgevt(event){
			var $ball = this.$refs.ball;
			$ball.style.top = event.pageY+'px';
			$ball.style.left = event.pageX+'px';
			$ball.style.transition = 'left 0s, top 0s';
			setTimeout(()=>{
				$ball.style.top = (window.innerHeight-50)+'px';
				$ball.style.left = '40px';
				$ball.style.zIndex = 1000;
				$ball.style.transition = 'left 1s linear, top 1s ease-in';
			}, 20)
			setTimeout(()=>{
				$ball.style.display ='none';
				$ball.style.top = window.innerHeight+'px';
				$ball.style.display ='block';
			}, 1000)
		},
		getSecondLevel(h){
			if(this.haveMore2){
				this.$post('PlatFormAPI/Doctor/QueryDepartmentOfSecondLevel',{
					DepartmentName:'',
					ParentId:-1,
					PageIndex: this.page2,
					PageSize: 10
				}).then(o => {
					if (!o.ReturnData || o.ReturnData.length < 10){
					    this.haveMore2 = false
					}
					else {
					    this.haveMore2 = true
					}                
					if (h){
					    o.ReturnData.forEach(v => {
					        this.Level.push(v)
					    })
					}
					else {
					    this.Level = o.ReturnData
						this.Level.unshift('')
					}
				})
			}
		},
		 pullList(h){
		    if(this.haveMore){
				this.$post('PlatFormAPI/SpecialistHosGroup/GetMallGoodsByName',{
					DrId:this.user.Data.User.DoctorId,
				    GoodsName:this.search,
					AreaId:this.AreaId,
					AreaName:this.AreaName,
					OrderByType: this.OrderByType,//012
					OrderDirection: this.OrderDirection,//(true:;false:)
					KeyWord:this.KeyWord,
				    PageIndex: this.page,
				    PageSize: 15
				}).then(o => {
				    this.pageShow = true
				    if (!o.ReturnData || o.ReturnData.length < 15){
				        this.haveMore = false
				    }
				    else {
				        this.haveMore = true
				    }                
				    if (h){
				        o.ReturnData.forEach(v => {
				            this.Goods.push(v)
				        })
				    }
				    else {
				        this.Goods = o.ReturnData
				    }
					if(this.ListDetails && this.ListDetails.length){
						this.Goods.forEach((v,k) => {
						   this.ListDetails.forEach((i,y) => {
							   if(v.GoodsId == i.GoodsId){
								   this.Goods.splice(k, 1, i)
							   }
						   })
						})
					}
				})
		        
		    }
		},
		add(i,idx,d,$event){
			if(d){
				this.ListDetails.forEach((v,k) => {
					if(i.GoodsSource == 3){
						if(v.GoodsId == i.GoodsId){   
							i.GoodsNum = v.GoodsNum + 1
							this.ListDetails.splice(k, 1, i)
						}
					}else{
						if(v.GoodsSource == i.GoodsSource && v.SpecialistGoodsId == i.SpecialistGoodsId && v.SpecialistHosGroupId == i.SpecialistHosGroupId && v.GoodsId == i.GoodsId){   
							i.GoodsNum = v.GoodsNum + 1
							this.ListDetails.splice(k, 1, i)
						}
					}
				})
				this.alertMsgevt($event)
			}else{
				this.yEvent = $event
				if(i.GoodsSpec && i.GoodsSpec.length > 1 && i.GoodsSource != 1){
					var t = 0
					this.ListDetails.forEach((v,k) => {
						i.GoodsSpec.forEach((a,y) => {
							if(v.GoodsSpecId == a.GoodsSpecId){
							   this.act2 = y
							   t = 1
							}
						})
					})
					if(t==0){
						this.act2 = null
					}
					this.spcData = i
					this.carShow = true
					return
				}else if(i.GoodsSource == 1){
					this.drSearch = ""
					this.spcData = i
					this.chooseDrShow = true
					this.pull()
					return
				}else if(i.GoodsSource == 4){
					if(this.ListDetails && this.ListDetails.length){
						let t = 0
						this.ListDetails.forEach((v,k) => {
						    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
								i.GoodsNum = v.GoodsNum + 1
								this.ListDetails.splice(k, 1, i)
								t = 1
							}
						})
						if(t == 0){
							i.GoodsNum = 1
							this.ListDetails.push(i)
						}
					}else{
						i.GoodsNum = 1
						this.ListDetails.push(i)
					}
					this.alertMsgevt($event)
				}else{
					if(this.ListDetails && this.ListDetails.length){
						let t = 0
						this.ListDetails.forEach((v,k) => {
						    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
								i.GoodsNum = v.GoodsNum + 1
								i.GoodsSpecId = v.GoodsSpecId
								i.GoodsSpecName = v.GoodsSpecName
								this.ListDetails.splice(k, 1, i)
								t = 1
							}
						})
						if(t == 0){
							i.GoodsNum = 1
							i.GoodsSpecId = i.GoodsSpec[0].GoodsSpecId
							i.GoodsSpecName = i.GoodsSpec[0].GoodsSpecName
							this.ListDetails.push(i)
						}
					}else{
						i.GoodsNum = 1
						i.GoodsSpecId = i.GoodsSpec[0].GoodsSpecId
						i.GoodsSpecName = i.GoodsSpec[0].GoodsSpecName
						this.ListDetails.push(i)
					}
					this.alertMsgevt($event)
				}
			}
		
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		addspc(a){
			if(a.GoodsNum){
				this.spcData.GoodsNum = a.GoodsNum + 1
			}else{
				this.spcData.GoodsNum = 1
			}
			this.carShow = false
			this.carShow = true
		},
		reducespc(a){
			if(this.spcData.GoodsNum <= 1){
				return 
			}
			this.spcData.GoodsNum = a.GoodsNum - 1
			this.carShow = false
			this.carShow = true
		},
		sSpecName(i,idx){
			this.act2 = idx
			this.spcData.GoodsSpecId = i.GoodsSpecId
			this.spcData.GoodsSpecName = i.GoodsSpecName
			this.spcData.VipPrice = i.VipPrice
		},
		carSure(a){
			if(!a.GoodsSpecId){
				return this.$toast('')
			}
			if(!this.spcData.GoodsNum){
				return this.$toast('')
			}
			this.carShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == a.GoodsSource && v.GoodsId == a.GoodsId && v.GoodsClassId == a.GoodsClassId && v.SpecialistGoodsId == a.SpecialistGoodsId){  
						t = 1
						a.GoodsNum = v.GoodsNum
						this.ListDetails.splice(k, 1, a)
					}
				})
				if(t == 0){
					a.GoodsNum = this.spcData.GoodsNum
					this.ListDetails.push(a)
				}
			}else{
				a.GoodsNum = this.spcData.GoodsNum
				this.ListDetails.push(a)
			}
			this.alertMsgevt(this.yEvent)
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		reduce(i,n){
			this.ListDetails.forEach((v,k) => {
			    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){ 
					i.GoodsNum = v.GoodsNum - 1
					if(i.GoodsNum < 1){
						this.ListDetails.splice(k, 1)
					}else{
						this.ListDetails.splice(k, 1, i)
					}
					if(!this.ListDetails.length){
						this.specsShow = false
					}
				}
			})
			console.log(this.ListDetails)
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		specsCar(){
			if(this.specsShow){
				this.specsShow = false
			}else{
				this.specsShow = true
			}
		},
		del(){
			this.specsShow = false
			this.ListDetails = []
			this.Goods.forEach((v,k) => {
			   v.GoodsNum = 0
			})
			this.$store.dispatch('pushpreList',[])
		},
		closeCar(a){
			this.carShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == a.GoodsSource && v.GoodsId == a.GoodsId && v.GoodsClassId == a.GoodsClassId && v.SpecialistGoodsId == a.SpecialistGoodsId){  
						t = 1
						a.GoodsNum = v.GoodsNum
						this.ListDetails.splice(k, 1, a)
					}
				})
				if(t == 0){
					a.GoodsNum = 0
				}
			}else{
				a.GoodsNum = 0
			}
		},
		sendSave(n){
			if(this.ListDetails && this.ListDetails.length){
			}else{
				return this.$toast('')
			}
			this.btnColor = false
			let List = []
			this.ListDetails.forEach(v => {
			    List.push({
					GoodsSource:v.GoodsSource?v.GoodsSource:3,	//(1-2-3-)
					GoodsId:v.GoodsId,	//Id/Id
					SpecialistGoodsId:v.GoodsSource == 3 ? 0:v.SpecialistGoodsId,//Id(1,2,4 30)
					SpecialistHosGroupId:v.SpecialistHosGroupId?v.SpecialistHosGroupId:0, //id
					HosGroupName:v.SpecialistHosGroupId?v.HosGroupName:'',  //
					GoodsName:v.GoodsName,	///
					GoodsNum:v.GoodsNum,	//
					DrId:v.DrId || 0,
					DrName:v.DrName||'',
					GoodsSpecId:v.GoodsSpecId || "",	//Id
					GoodsSpecName:v.GoodsSpecName || "",	//
			    })
			})
			let url_ = ''
			if(n){
				url_ = 'PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommendUsed'
			}else{
				url_ = 'PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommend'
			}
			this.$post(url_,{
				GoodsRecomId:0,	//Id
				DrId:this.user.Data.User.DoctorId,
				DrName:this.user.Data.User.DoctorName,	//
				GoodsRecomDetail:List,
			}).then(o => {
				if(!o){
					return
				}
				if(n){
					this.$toast("")
					this.btnColor = true
				}else{
					this.$store.dispatch('pushrecomMsg',{
						DataSource:3,
						DataId: o,//Id
						fromIM:this.$store.state.recomMsg?this.$store.state.recomMsg.fromIM:0
					})
					this.$router.replace(`/prefRecommend/reDetail`)	
				}				
			})
			
		},
		pull(){
			this.pageShow = false
		    this.$post('PlatFormAPI/SpecialistHosGroup/GetSpecialistDoctorList',{
				DrId:this.user.Data.User.DoctorId,
		       SpecialistHosGroupId:this.SpecialistHosGroupId,	//Id
		       GoodsClassId:this.spcData.GoodsClassId,	//Id
		       GoodsClassCode:this.spcData.GoodsClassCode,	//Code
		       ServiceItemId:this.spcData.GoodsId,	//Id
		       DoctorName:this.drSearch,	//
		       PageIndex: this.page,
		       PageSize: 100
		    }).then(o => {
		        this.list = o.ReturnData
				var t = 0
				console.log(this.ListDetails)
				this.ListDetails.forEach((v,k) => {
					o.ReturnData.forEach((a,y) => {
						if(v.GoodsClassId == this.spcData.GoodsClassId && v.GoodsId == this.spcData.GoodsId&& a.DrId == v.DrId && v.SpecialistGoodsId == this.spcData.SpecialistGoodsId){
						   this.act3 = y
						   t = 1
						}
					})
				})
				if(t==0){
					this.act3 = null
				}
				this.pageShow = true
		    })
		},
		drNo(){
			this.ListDetails.forEach((v,k) => {
				if(v.GoodsClassId == this.spcData.GoodsClassId && v.GoodsId == this.spcData.GoodsId&& this.spcData.DrId == v.DrId && v.SpecialistGoodsId == this.spcData.SpecialistGoodsId){
					this.ListDetails.splice(k, 1)
				}
			})
			this.$store.dispatch('pushpreList',this.ListDetails)
			this.spcData.GoodsNum = 0
			this.spcData = null
			this.chooseDrShow = false
		},
		drIcon(c,idx){
			this.act3 = idx
			this.spcData.GoodsNum = 1
			this.spcData.DrId = c.DrId
			this.spcData.DrName = c.DrName
			this.spcData.Price = c.Price
			this.spcData.PositionName = c.PositionName
			this.spcData.HospitalName = c.HospitalName
		},
		drYes(){
			if(this.act3 == null){
				return this.$toast('')
			}
			this.chooseDrShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == this.spcData.GoodsSource && v.GoodsId == this.spcData.GoodsId && v.GoodsClassId == this.spcData.GoodsClassId && v.SpecialistGoodsId == this.spcData.SpecialistGoodsId){  
						t = 1
						this.ListDetails.splice(k, 1, this.spcData)
					}
				})
				if(t == 0){
					this.ListDetails.push(this.spcData)
				}
			}else{
				this.ListDetails.push(this.spcData)
			}
			this.alertMsgevt(this.yEvent)
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		searchFor2(){
			this.pull()
		},
        searchFor(){
			this.page = 1
			this.haveMore = true
			this.pullList()
        },

        getMore(){
            const el = this.$refs.el
            let timer = null
            el.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
							const top = el.scrollTop ||
								el.pageYOffset ||
								el.scrollTop,
								h = el.scrollHeight,
								wh = window.innerHeight
                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pullList(true)
                    }
                }, 100)
            }
			
        },
		getMore2(){
			const el2 = this.$refs.el2
			let timer2 = null
			el2.onscroll = () => {
				clearTimeout(timer2)
				timer2 = setTimeout(() => {
					const top = el2.scrollTop ||
						el2.pageYOffset ||
						el2.scrollTop,
						h = el2.scrollHeight,
						wh = window.innerHeight
						this.tTop = top
					if(h - top - wh < -90 && this.haveMore2) {
						this.page2 += 1
						this.getSecondLevel(true);
					}
				}, 100)
			}
		}

    }
}



